.filter-country {
  margin: auto;
  background-color: $filter-color;
  padding-top: 22px;
  padding-bottom: 15px;
  padding-left: 25px;
  padding-right: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 270px;
}


.filter-country__show-all {
  @media (max-width: $mobile-width-only) {
    display: none;
  }
}


.filter-country__subtitle {
  position: relative;
  color: $text-secondary-color;
  font-weight: bold;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  margin: 0;

  &::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 10px;
    left: -20px;
    top: 3px;
    background-image: url("../../img/icon_travelers.svg");
  }

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.filter-country__wrapper-filter {
  width: 270px;
  display: flex;
  justify-content: space-between;
  padding-left: 27px;
  padding-right: 4px;
  box-sizing: border-box;
}

.filter-country__button {
  width: 25px;
  height: 18px;
  background-color: transparent;
  background-image: url("../../img/icon_filter_expand.svg");
  background-position: center;
  background-repeat: no-repeat;
  border: none;
}

.filter-country__wrapper {

}

.filter-country__item {
  font-weight: bold;
  font-size: 16px;
  line-height: 18px;
  color: $text-secondary-color;
  list-style-type: none;
  cursor: pointer;
  margin-bottom: 5px;
}

.filter-country__item-button {
  font-family: inherit;
  font-weight: bold;
  font-size: 16px;
  line-height: 18px;
  color: $text-secondary-color;
  opacity: 0.3;
  background-color: $filter-color;
  border: none;
  text-transform: uppercase;
}

.filter-country__item-button__active {
  opacity: 1;
}

.filter-country__list {
  display: flex;
  flex-direction: column;
  padding-left: 25px;
}


.filter-country__table {
  border-collapse: collapse;
  margin-bottom: 300px;
  margin-left: 25px;
}

.filter-country__line {

}

.filter-country__cell {
  border: 1px solid $border-color;
  padding: 0;
}

.filter-country__nucleus {
  width: 43px;
  height: 40px;
}

.filter-country__nucleus-button {
  font-family: inherit;
  width: 43px;
  height: 40px;
  border: none;
  background-color: $white-color;
  font-size: 14px;
  line-height: 14px;
}

.filter-country__nucleus-list {
  display: none;
  list-style-type: none;
  min-width: 150px;
  padding: 0;
}

.filter-country__nucleus-button__active {
  background-color: $header-color;
  color: $white-color;
}

.filter-country__nucleus-button__active + .filter-country__nucleus-list {
  display: block;
  margin-top: 180px;
}

.filter-country__nucleus-item {

}

.filter-country__nucleus-link {
  text-decoration: none;
  color: $dark-color;
}


.filter-country__close-button {
  font-family: inherit;
  position: relative;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  padding-left: 109px;
  padding-right: 83px;
  padding-top: 10px;
  padding-bottom: 10px;
  border: none;
  background-color: $close-button-color;
  border-radius: 20px;
  color: $text-secondary-color;

  &::before {
    content: "";
    position: absolute;
    width: 14px;
    height: 14px;
    top: 12px;
    left: 85px;
    background-image: url("../../img/icon_close_filter.svg");
    background-repeat: no-repeat;
    background-size: auto;
  }
}
