.first-screen {
  position: relative;
  padding-bottom: 39px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background:url("../../img/traveller_illustration_mobile.png") 55% 18% no-repeat,
    url("../../img/globe_back_mobile.jpg") 50% -24% no-repeat,
    $auxiliary-color;
  background-size: auto;
  z-index: 11;

  @media (min-resolution: 2dppx) {
    background:url("../../img/traveller_illustration_mobile@2x.png") 55% 18% no-repeat,
      url("../../img/globe_back_mobile@2x.jpg") 50% -105px no-repeat,
      $auxiliary-color;
    background-size: 192px auto, 320px auto, auto;
  }

  @media (min-width: $tablet-width) {
    padding-bottom: 96px;
    background:url("../../img/traveller_illustration_tablet.png") 55% 45% no-repeat,
      url("../../img/globe_back_tablet.jpg") 50% 50% no-repeat,
      $header-color;

    @media (min-resolution: 2dppx) {
      background:url("../../img/traveller_illustration_tablet@2x.png") 55% 45% no-repeat,
        url("../../img/globe_back_tablet@2x.jpg") 50% 50% no-repeat,
        $header-color;
      background-size: 372px auto, 768px auto, auto;
    }
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 123px;
    background:url("../../img/traveller_illustration_desktop.png") 55% 45% no-repeat,
      url("../../img/globe_back_desktop.jpg") 50% 50% no-repeat,
      $desktop-color;
    background-size: 431px auto, 1440px auto, auto;

    @media (min-resolution: 2dppx) {
      background:url("../../img/traveller_illustration_desktop@2x.png") 55% 45% no-repeat,
        url("../../img/globe_back_desktop@2x.jpg") 50% 50% no-repeat,
        $desktop-color;
      background-size: 431px auto, 1440px auto, auto;
    }
    border-radius: 0 0 40px 40px;
  }
}

.first-screen__title {
  width: 238px;
  font-weight: bold;
  font-size: 36px;
  line-height: 40px;
  margin: auto;
  text-align: center;
  margin-bottom: 245px;
  padding-left: 41px;
  padding-right: 41px;
  margin-left: auto;
  margin-right: auto;

  @media (min-width: $tablet-width) {
    width: 399px;
    font-size: 60px;
    line-height: 70px;
    margin-bottom: 86px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 52px;
    padding-right: 0;
    margin: 0;
  }
}

.first-screen__wrapper {
  margin: 0;

  @media (min-width: $tablet-width) {
    width: 100%;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: row;
    width: 1320px;
    justify-content: space-between;
    margin: auto;
    margin-top: 110px;
    margin-bottom: 101px;
  }
}

.first-screen__contacts {
  min-height: 40px;
}

.first-screen__contacts-list {
  display: flex;
  flex-direction: row;
  list-style-type: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
}

.first-screen__contacts-item {
  margin: 0;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.first-screen__phone {
  background-color: $secondary-color;
  padding-left: 10px;
  padding-right: 11px;
  padding-top: 13px;
  padding-bottom: 8px;
  border-radius: 50%;

  @media (min-width: $desktop-width) {
    padding-top: 4px;
    padding-bottom: 3px;
  }
}

.first-screen__mail {
  background-color: $special-color;
  padding-left: 10px;
  padding-right: 11px;
  padding-top: 13px;
  padding-bottom: 8px;
  border-radius: 50%;

  @media (min-width: $desktop-width) {
    padding-top: 4px;
    padding-bottom: 3px;
  }
}

.first-screen__login {
  background-color: $light-color;
  font-weight: bold;
  font-size: 17px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: $text-secondary-color;
  padding-left: 39px;
  padding-right: 39px;
  padding-top: 12px;
  padding-bottom: 8px;
  border-radius: 25px;
}


.first-screen__wrapper-quote {
  display: flex;
  flex-direction: column;
  max-width: 300px;
  padding-left: 50px;
  margin: auto;
  margin-top: 26px;

  @media (min-width: $tablet-width) {
    margin-bottom: 211px;
    padding-right: 400px;
    width: 265px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    padding: 0;
    padding-right: 8px;
  }
}

.first-screen__quote-text {
  margin: 0;
  margin-bottom: 18px;
  width: 151px;

  @media (min-width: $tablet-width) {
    width: 264px;
    font-weight: 300;
  }
}

.first-screen__quote-copyright {
  font-weight: bold;
  margin: 0;
  margin-bottom: 29px;
  width: 161px;

  @media (min-width: $tablet-width) {
    width: 264px;
    font-size: 21px;
    line-height: 25px;
  }
}

.first-screen__list {
  padding-left: 25px;
  padding-right: 25px;
  margin: auto;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 369px 344px;
    padding-left: 20px;
    padding-right: 0;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(4, 1fr);
    padding-left: 60px;
    padding-right: 60px;
  }
}

.first-screen__item {
  display: flex;
  justify-content: space-between;
  padding-top: 22px;
  padding-bottom: 62px;
  padding-left: 24px;
  padding-right: 21px;
  width: 225px;
  color: $auxiliary-color;
  list-style-type: none;
  border-radius: 20px;
  margin-top: -40px;

  @media (min-width: $tablet-width) {
    box-sizing: border-box;
    flex-direction: column;
    padding-top: 31px;
    padding-left: 33px;
    padding-bottom: 21px;
    padding-right: 33px;
    margin-top: 0;
  }
}

.first-screen__item-one {
  background-color: $secondary-color;
  margin-top: 0;

  @media (min-width: $tablet-width) {
    width: 369px;
    border-radius: 20px 0 0 0;
  }

  @media (min-width: $desktop-width) {
    width: 345px;
    border-radius: 20px 0 0 20px;
  }
}

.first-screen__item-two {
  background-color: $secondary-two-color;

  @media (min-width: $tablet-width) {
    margin-left: -25px;
    width: 344px;
    border-radius: 20px 20px 0 0;
  }

  @media (min-width: $desktop-width) {
    margin-left: -30px;
    width: 375px;
    border-radius: 20px 0 0 20px;
  }
}

.first-screen__item-three {
  background-color: $special-color;

  @media (min-width: $tablet-width) {
    width: 369px;
    border-radius: 0 0 0 20px;
  }

  @media (min-width: $desktop-width) {
    margin-left: -30px;
    width: 375px;
    border-radius: 20px 0 0 20px;
  }
}

.first-screen__item-four {
  background-color: $light-color;
  padding-bottom: 22px;

  @media (min-width: $tablet-width) {
    margin-left: -25px;
    width: 344px;
    border-radius: 0 0 20px 20px;
  }

  @media (min-width: $desktop-width) {
    margin-left: -30px;
    width: 315px;
    border-radius: 20px;
  }
}

.first-screen__item-description {
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
  margin: 0;

  @media (min-width: $tablet-width) {
    font-size: 24px;
    line-height: 24px;
    padding-bottom: 10px;
  }
}

.first-screen__item-number {
  color: $white-color;
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    line-height: 30px;
  }
}
